// utils/formatBlog.ts

export interface RawBlog {
  id: number;
  title: string;
  body: string;
  userId: number;
}

export interface Blog {
  id: number;
  title: string;
  body: string;
  userId: number;
  image: string;
  authorImage: string;
  authorName: string;
  date: string;
  slug: string;
}

export function formatBlog(raw: RawBlog, index: number): Blog {
  return {
    ...raw,
    image: `/blog${index + 1}.png`,
    authorImage: "/career.png",
    authorName: `User ${raw.userId}`,
    date: "July 10, 2023",
    slug: `blog-${raw.id}`,
  };
}
