// app/blog/page.tsx

import React from "react";
import Link from "next/link";
import FallbackImage from "../../components/common/FallbackImage";
import Title from "../../components/common/Title";
import { formatBlog, Blog } from "../../utils/formatBlog";

export default async function BlogListPage() {
  const res = await fetch("https://jsonplaceholder.typicode.com/posts");
  const data = await res.json();

  const blogs: Blog[] = data.slice(0, 9).map((item: any, i: number) => formatBlog(item, i));

  return (
    <div className="py-10 bg-[#131321] text-white">
      <div className="main-padding">
        <div className="text-center mb-2">
          <Title items={["Recommended topics"]} />
        </div>
        <hr className="border border-gray-700" />

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-10 py-10">
          {blogs.map((blog) => (
            <div key={blog.id} className="card">
              <div className="h-[300px] w-full overflow-hidden rounded-lg">
                <FallbackImage
                  src={blog.image}
                  alt={blog.title}
                  width={300}
                  height={300}
                  className="w-full h-full object-cover"
                />
              </div>
              <div className="pt-7">
                <Link href={`/blog/${blog.slug}`}>
                  <h6 className="text-2xl font-medium hover:text-blue-400 transition">
                    {blog.title}
                  </h6>
                </Link>
                <p className="py-5 text-gray-300">
                  {blog.body.slice(0, 150)}...
                </p>
                <div className="flex gap-4 items-center">
                  <FallbackImage
                    src={blog.authorImage}
                    width={50}
                    height={50}
                    alt={blog.authorName}
                    className="rounded-full w-14 h-14"
                  />
                  <div>
                    <p className="text-[#3B5CFF] font-semibold">
                      {blog.authorName}
                    </p>
                    <p className="text-sm text-gray-400">{blog.date}</p>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
